import React from 'react';
import '../styles/Film.scss';
import {KEY, URL_SELECTED} from "../utils/utils";
const Film = ({selectedFilm, getFilms, list}) => {
    React.useEffect(() => {
        window.scrollTo(0, 650);
        getFilms(URL_SELECTED, KEY, selectedFilm);
    }, [selectedFilm]);
    return (
        <div className="Film">
            <img className="Film__poster" src={list.posterUrl} alt=""/>
            <div className="container">
                <div className="Film__display">
                    <div className="Film__picture"><img src={list.posterUrlPreview}></img></div>
                    <ul className="Film__information">
                        <li className="Film__item">{list.nameRu || list.nameEn || list.nameOriginal || '–ù–µ—Ç –Ω–∞–∑–≤–∞–Ω–∏—è'}</li>
                        <li className="Film__item">–≥–æ–¥: {list.year}</li>
                        <li className="Film__item">–¥–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å —Ñ–∏–ª—å–º–∞: {list.filmLength} –º–∏–Ω—É—Ç</li>
                        <li className="Film__item">{list.countries && list.countries.length > 1 ? '—Å—Ç—Ä–∞–Ω—ã' : '—Å—Ç—Ä–∞–Ω–∞'}: {list.countries && list.countries.map(country => country['country' || 0]).join(', ')}</li>
                        <li className="Film__item">–∂–∞–Ω—Ä—ã: {list.genres && list.genres.map(genre => genre['genre' || 0]).join(', ')}</li>
                        <li className="Film__item">—Ä–µ–π—Ç–∏–Ω–≥: {list.ratingKinopoisk || list.ratingImdb}</li>
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default Film;